import FilterListIcon from '@mui/icons-material/FilterList';
import { Autocomplete, Button, Chip, Stack, TextField, Typography } from '@mui/material';
import React, { useEffect, useState } from 'react';

import { IVehiclesBrand } from '../../../../types';
import styles from './_filter.module.scss';

export const Filters: React.FC<{
  vehiclesBrands: IVehiclesBrand | null;
  vehiclesBrandsRequest: boolean;
}> = ({
  vehiclesBrands,
  vehiclesBrandsRequest
}) => {
  const [isShowFilterContent, setIsShowFilterContent] = useState<boolean>(false);
  const [selectedBrands, setSelectedBrands] = React.useState<{ title: string }[]>([]);

  const eventIsShowFilterContent = (): void => {
    return setIsShowFilterContent(!isShowFilterContent);
  };

  const generatingBrandsObject = (brands: IVehiclesBrand | null): { title: string }[] => {
    return brands ? brands.map(brand => ({title: brand})) : [];
  };

  useEffect(() => {
    if (selectedBrands.length) {
      console.log(selectedBrands);
    }
  }, [selectedBrands]);

  return (
    <div className={styles.content}>
      <div className={styles.content_header}>
        <div className={styles.chips}>
          <Typography variant="body1">Filters</Typography>
          <Stack direction="row" spacing={1} className={styles.chips_stack}>
            <Chip
              label="BMW"
              variant="outlined"
              onClick={() => []}
              onDelete={() => []}
            />
            <Chip
              label="Peogeot"
              variant="outlined"
              onClick={() => []}
              onDelete={() => []}
            />
          </Stack>
          <Button variant="outlined">Reset</Button>
        </div>
        <Button
          variant="outlined"
          startIcon={<FilterListIcon/>}
          onClick={() => eventIsShowFilterContent()}
        >More Filters</Button>
      </div>
      {isShowFilterContent && (
        <div className={styles.content_body}>
          <Stack direction="row" spacing={4}>
            <Autocomplete
              multiple
              id="brand"
              loading={vehiclesBrandsRequest}
              options={generatingBrandsObject(vehiclesBrands)}
              isOptionEqualToValue={(option, value) => option.title === value.title}
              onChange={((event, value) => setSelectedBrands(value))}
              getOptionLabel={(option) => option.title}
              filterSelectedOptions
              sx={{width: 320}}
              renderTags={(value, getTagProps) =>
                value.map((option, index) => (
                  <Chip
                    variant="outlined"
                    label={option.title}
                    {...getTagProps({index})}
                  />
                ))
              }
              renderInput={(params) => (
                <TextField
                  {...params}
                  label="Brand"
                  placeholder="Brand"
                />
              )}
            />
            <Autocomplete
              multiple
              id="tags-outlined"
              options={generatingBrandsObject(vehiclesBrands)}
              getOptionLabel={(option) => option.title}
              filterSelectedOptions
              sx={{width: 320}}
              renderTags={(value, getTagProps) =>
                value.map((option, index) => (
                  <Chip
                    variant="outlined"
                    label={option.title}
                    {...getTagProps({index})}
                  />
                ))
              }
              renderInput={(params) => (
                <TextField
                  {...params}
                  label="Model"
                  placeholder="Model"
                />
              )}
            />
            <TextField
              label="VIN"
              sx={{width: 320}}
              placeholder="Search by VIN"
            />
          </Stack>
        </div>
      )}
    </div>
  );
};
