import {
  Box,
  Breadcrumbs,
  Container,
  Grid,
  Step,
  StepButton,
  Stepper,
  Typography
} from '@mui/material';
import React from 'react';
import { Link, useParams } from 'react-router-dom';

import { TitleBar } from '../../components/molecules';

export const DetectorPage: React.FC = () => {
  const params = useParams();

  const steps = ['Speed', 'Equal', '0mph'];

  return (
    <Container maxWidth={false}>
      <Grid lg={12}>
        <Breadcrumbs aria-label="breadcrumb">
          <Link color="inherit" to="/detectors">Detectors</Link>
          <Typography color="text.primary">{params.id}</Typography>
        </Breadcrumbs>
      </Grid>
      <Grid lg={12}>
        <TitleBar title={`Detector ID ${params.id}`}/>
        <Typography variant="h6" sx={{mt: '24px'}}>Logic</Typography>
        <Box sx={{
          maxWidth: 450,
          border: '1px solid #FF9800',
          borderRadius: '4px',
          p: '10px',
          mt: '24px'
        }}>
          <Stepper nonLinear>
            {steps.map((label, index) => (
              <Step key={label}>
                <StepButton color="inherit">
                  {label}
                </StepButton>
              </Step>
            ))}
          </Stepper>
        </Box>
        <Box sx={{
          maxWidth: 450,
          border: '1px solid #FF9800',
          borderRadius: '4px',
          p: '10px',
          mt: '24px'
        }}>
          <Stepper nonLinear orientation="vertical">
            {steps.map((label, index) => (
              <Step key={label}>
                <StepButton color="inherit">
                  {label}
                </StepButton>
              </Step>
            ))}
          </Stepper>
        </Box>
      </Grid>
    </Container>
  );
};
