import { Container, Grid } from '@mui/material';
import { DataGrid, GridRowsProp } from '@mui/x-data-grid';
import React, { useEffect, useState } from 'react';

import { TitleBar } from '../../components/molecules';
import { vehiclesTableHeadName } from '../../constants';
import { useActionsHook, useTypedSelectorHook } from '../../hooks';
import { useStyles } from './_MakeStyles.constants';
import { Filters } from './fragments';

export const VehiclesPage: React.FC = () => {
  const classes = useStyles();
  const {
    vehicles,
    vehiclesBrands,
    vehiclesBrandsRequest
  } = useTypedSelectorHook(({vehicles}) => vehicles);
  const [vehiclesList, setVehiclesList] = useState<GridRowsProp>([]);
  const {getVehiclesActions, getVehiclesBrandAction} = useActionsHook();

  useEffect(() => {
    getVehiclesActions();
    getVehiclesBrandAction();
  }, []);

  useEffect(() => {
    if (vehicles) setVehiclesList(vehicles);
  }, [vehicles]);

  return (
    <Container maxWidth={false}>
      <Grid item>
        <TitleBar title="Vehicles"/>
      </Grid>
      <Grid item>
        <Filters
          vehiclesBrands={vehiclesBrands}
          vehiclesBrandsRequest={vehiclesBrandsRequest}
        />
        <DataGrid
          autoHeight
          disableColumnMenu
          className={classes.root}
          hideFooterSelectedRowCount={false}
          rows={vehiclesList}
          columns={vehiclesTableHeadName.map((column) => ({
            ...column,
            sortable: false
          }))}
          checkboxSelection
          getRowClassName={(params) =>
            `super-app-theme--${params.getValue(params.id, 'is_checked')}`
          }
        />
      </Grid>
    </Container>
  );
};
