import EditIcon from '@mui/icons-material/Edit';
import { IconButton, Switch } from '@mui/material';
import { GridColDef, GridRenderCellParams } from '@mui/x-data-grid';
import React from 'react';
import { Link } from 'react-router-dom';

import { ImpactRate } from '../components/molecules';
import { IDetectorsProps } from '../types/store_types';

/**
 * Пункты заголовков таблицы Detectors
 */

// Todo Egor: Switch defaultChecked значение должно быть сразу true || false
export const detectorsTableHeadName: GridColDef[] = [
  {
    field: 'is_active',
    headerName: 'Activity',
    renderCell: ((params: GridRenderCellParams<IDetectorsProps>) =>
        <Switch defaultChecked={!!params.value}/>
    )
  },
  {field: 'id', headerName: 'Detector ID'},
  {
    field: 'alert_name',
    headerName: 'Alert name',
    renderCell: (((params: GridRenderCellParams<IDetectorsProps>) =>
        <Link to={`/detector/${params.row.id}`}>
          {params.value}
        </Link>
    ))
  },
  {field: 'alert_description', headerName: 'Description'},
  {field: 'developer', headerName: 'Developer'},
  {
    width: 200,
    field: 'impact_rate',
    headerName: 'Impact rate',
    renderCell: ((params: GridRenderCellParams<IDetectorsProps>) =>
        <ImpactRate type={params.value.toString()}/>
    )
  },
  {field: 'last_updated', headerName: 'Last update'},
  {field: 'trigger', headerName: 'Triggering'},
  {
    field: 'edit',
    headerName: 'Edit',
    renderCell: (((params: GridRenderCellParams<IDetectorsProps>) =>
        <Link to={`/detector-editor/${params.row.id}`}>
          <IconButton aria-label="delete" size="medium">
            <EditIcon fontSize="inherit"/>
          </IconButton>
        </Link>
    ))
  }
];
